.loginForm{width:400px;margin:0 auto;padding:2rem;border:1px solid #eee;border-radius:10px;background:#fff;margin-bottom:100px;margin-top:100px}.toggleButtons{display:flex;gap:1rem;margin-bottom:1rem}.active{background:#4f46e5;color:#fff}.inputGroup{margin-bottom:1rem}.input{width:100%;padding:.5rem;border-radius:6px;border:1px solid #ccc;margin-bottom:1rem}.options{display:flex;justify-content:space-between;align-items:center;margin-bottom:1rem}.loginButton{width:100%;padding:.75rem;background-color:#4f46e5;color:#fff;border:none;border-radius:6px;cursor:pointer}.registerPrompt{margin-top:1rem;text-align:center}