.registerPage{margin:0}.registerHeader{height:150px;display:flex;justify-content:space-between;align-items:center}.registerHeader.light{background-color:var(--surface)}.registerHeader.dark{background-color:var(--dark)}.registerContainer{width:100%;max-width:1200px;margin-left:auto;margin-right:auto;padding-left:15px;padding-right:15px;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center}.registerTitle{display:flex;justify-content:space-between;align-items:center;width:100%}.registerTitle h2{margin:0;font-size:1.5rem}.registerTitle h2.light{color:var(--on-surface)}.registerTitle h2.dark{color:var(--surface)}.registerTitle p{margin:0;font-size:1rem;opacity:.7}.registerTitle p.light{color:var(--secondary)}.registerTitle p.dark{color:var(--secondary2)}